import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardActions from '@material-ui/core/CardActions';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        position: "relative"
    },
    media: {
        height: "200px",
        filter: "brightness(0.5)"
    },
    cardActionButtons: {
        flexDirection: "row-reverse",
        backgroundColor: "transparent",
        position: "absolute",
        width: "100%",
        top: "0",
        zIndex: "9999"
    },
    cardInfoBar: {
        backgroundColor: "transparent",
        position: "absolute",
        width: "100%",
        bottom: "0",
        color: "#fff"
    },
    margin: {
        transform: "scale(0.75, 0.75)"
    },
}));

export default function MiniChapter(props) {
    const classes = useStyles();

    function addChaptersDefaultSrc (ev) {
        ev.target.src = `./chapters/default.png`
    }
    
    return (
        <Grid item lg={props.cardSize} xs={12}>
            <Card className={classes.root} onClick={props.onClick}>
                <CardActions disableSpacing className={classes.cardActionButtons}>
                    <Fab size="small" aria-label="Twitter" color="primary" className={classes.margin} onClick={(e) => {console.log("Twitter"); e.preventDefault();}}>
                        <TwitterIcon />
                    </Fab>
                    <Fab size="small" aria-label="Facebook" color="primary" className={classes.margin} onClick={(e) => {console.log("Facebook");  e.preventDefault();}}>
                        <FacebookIcon />
                    </Fab>
                </CardActions>
                <CardMedia
                    className={classes.media}
                    component="img"
                    src={ "./chapters/" + props.data.TeamPic}
                    alt={props.data.Campus}
                    onError={addChaptersDefaultSrc} 
                />
                <CardHeader
                    className={classes.cardInfoBar}
                    size="small"
                    title={props.data.Acronym}
                    onClick={props.onClick}
                />
            </Card>
            {
                props.spacing === 0 && (
                    <span>
                    <br></br>
                    </span>
                )
            }
        </Grid>
    );
}